.home_head {
	background: #333136e3;
	height: 8vh;
	border-bottom: 0.13em solid #444247e7;
	display: grid;
	grid-template-columns: repeat(4, 1fr) 18%;
}
.home_head .hide-small {
	display: none;
}
.home_head .logoLink {
	grid-column: 1/2;
	width: 100%;
}
.home_head .logoLink .head_logo {
	width: inherit;
}
.home_head .logoLink .head_logo img {
	width: 69%;
}
@media screen and (min-width: 530px) {
	.home_head .logoLink .head_logo img {
		width: 50%;
	}
}
.home_head h1 {
	grid-column: 2/5;
	color: #ffffff;
	font-size: 2em;
	align-self: center;
	font-family: "Lobster Two", cursive;
	text-transform: capitalize;
	justify-self: center;
	text-shadow: -1px -1px 0 #920d8b, 1px -1px 0 #920d8b, -1px 1px 0 #920d8b, 1px 1px 0 #920d8b;
}
@media screen and (min-width: 760px) {
	.home_head h1 {
		font-size: 2.5em;
	}
}
.home_head .burg_btn {
	grid-column: 5/6;
	padding: 13px 20%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: fit-content;
	cursor: pointer;
	gap: 10px;
}
.home_head .burg_btn div {
	background: #ffffff;
	width: 100%;
	height: 9px;
	border-radius: 30px;
	border: 0.13em solid #444247e7;
}
.home_head #closed .topBun {
	transform: translateY(0px) rotate(0deg);
	transition: transform 420ms ease;
}
.home_head #closed .botBun {
	transform: translateY(0px) rotate(0deg);
	transition: transform 420ms ease;
}
.home_head #open {
	padding: 18px 20%;
	gap: 0%;
}
.home_head #open .topBun {
	transform: translateY(5px) rotate(-495deg);
	transition: transform 420ms ease;
}
.home_head #open .botBun {
	transform: translateY(-5px) rotate(-405deg);
	transition: transform 420ms ease;
}
.home_head #active {
	display: block;
	position: fixed;
	height: 95vh;
	top: 8%;
	width: 100%;
	background: #1d1c1e;
	z-index: 2;
	opacity: 0%;
	transition: opacity 420ms ease;
}
.home_head #active ul {
	display: flex;
	flex-direction: column;
	height: 80%;
	justify-content: space-between;
	text-align: center;
	padding: 80px;
	position: relative;
	transform: translateX(-100%);
	transition: transform 420ms ease;
}
.home_head #active ul a {
	text-decoration: none;
	color: #ffffff;
	text-shadow: -1px -1px 0 #920d8b, 1px -1px 0 #920d8b, -1px 1px 0 #920d8b, 1px 1px 0 #920d8b;
	font-family: "Lobster Two", cursive;
	font-size: 4.2em;
	text-transform: capitalize;
}
@media screen and (min-width: 760px) {
	.home_head {
		grid-template-columns: repeat(6, 1fr) 15%;
	}
	.home_head h1 {
		grid-column: 3/6;
	}
	.home_head .burg_btn {
		grid-column: 7/8;
	}
}
@media screen and (min-width: 1020px) {
	.home_head {
		grid-template-columns: repeat(11, 1fr);
		z-index: 2;
	}
	.home_head .logoLink {
		grid-column: 1/2;
		z-index: 2;
	}
	.home_head h1 {
		grid-column: 2/5;
		font-size: 3em;
		justify-self: left;
	}
	.home_head .hide-small {
		grid-column: 8/12;
		display: flex;
		opacity: 100%;
	}
	.home_head .hide-small ul {
		display: flex;
		flex-direction: row;
		width: 100%;
		justify-content: space-between;
		align-items: center;
		padding: 0% 5%;
	}
	.home_head .hide-small ul a {
		text-decoration: none;
		text-transform: capitalize;
		font-family: "Lobster Two", cursive;
		color: #ffffff;
		font-size: 2em;
		text-shadow: -1px -1px 0 #920d8b, 1px -1px 0 #920d8b, -1px 1px 0 #920d8b, 1px 1px 0 #920d8b;
		transition: text-shadow 420ms ease-in-out;
	}
	.home_head .hide-small ul a:hover {
		text-shadow: 0px 0px 1.3rem #920d8b, 0px 0px 1.3rem #920d8b, 0px 0px 1.3rem #920d8b, 0px 0px 1.3rem #920d8b;
	}
	.home_head .hide-large {
		display: none;
	}
}
@media screen and (min-width: 1400px) {
