		background-color: #ffffff;
		border-radius: 50%;
		width: fit-content;
		border: 0.13em solid gold;
	}
	.home_head .logoLink .head_logo img {
		width: 85px;
	}
	.home_head .logoLink:hover {
		box-shadow: 0px 0px 1.3rem #920d8b, 0px 0px 1.3rem #920d8b, 0px 0px 1.3rem #920d8b, 0px 0px 1.3rem #920d8b;
		border-radius: 100%;
		transition: box-shadow 4200ms ease-in-out;
	}
}

