.followTray {
	background: #333136e3;
	border-top: 0.13em solid #444247e7;
	position: fixed;
	width: 100%;
	height: 6vh;
	bottom: 0%;
	z-index: 2;
}
.followTray ul {
	display: grid;
	grid-template-columns: minmax(1.3%, 7%) repeat(3, 1fr) minmax(1.3%, 7%);
	height: 100%;
	column-gap: 1.3em;
}
.followTray ul li {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 4.2px 0%;
}
.followTray ul li a {
	height: 100%;
	width: 100%;
	border: 0.13em solid rgba(255, 217, 0, 0.822);
	border-radius: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-decoration: none;
}
.followTray ul li a figure {
	display: flex;
	justify-content: center;
}
.followTray ul li a figure i {
	font-size: 1.3em;
	color: #dfdfdf;
	text-decoration: none;
}
@media screen and (min-width: 760px) {
	.followTray ul li a figure i {
		font-size: 2em;
	}
}
.followTray ul .followTray_link1 {
	grid-column: 2/3;
}
.followTray ul .followTray_link2 {
	grid-column: 3/4;
}
.followTray ul .followTray_link2 img {
	width: 30px;
}
.followTray ul .followTray_link3 {
	grid-column: 4/5;
}
@media screen and (min-width: 1020px) {
	.followTray {
		background: #333136;
		left: 0%;
		width: fit-content;
		height: 100vh;
		display: flex;
		border-top: none;
		border-right: 0.13em solid #444247e7;
		z-index: 0;
	}
	.followTray ul {
		grid-template-columns: 1.3px 1fr 1.3px;
		grid-template-rows: repeat(3, 1fr) minmax(1.3%, 7%);
		height: 33%;
		margin-top: auto;
		row-gap: 1em;
		column-gap: 0.42em;
	}
	.followTray ul li {
		padding: 0%;
		width: 50px;
		height: 66.6px;
	}
	.followTray ul .followTray_link1 {
		grid-area: 1/2/2/3;
	}
	.followTray ul .followTray_link2 {
		grid-area: 2/2/3/3;
	}
	.followTray ul .followTray_link3 {
		grid-area: 3/2/4/3;
	}
}

