.home_main {
	text-align: center;
	height: fit-content;
	padding: 30px 10%;
	margin: 0% 0%;
	background: linear-gradient(-45deg, #333136, #1d1c1e, #333136, #1d1c1e);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	border-radius: 10px;
	overflow: hidden;
}
.home_main h3 {
	text-transform: capitalize;
	font-size: 2em;
	margin: 30px 0% 80px 0%;
	color: #ffffff;
	font-family: "Lobster Two", cursive;
	transition: text-shadow 420ms ease-in-out;
	animation: textShadowPulse 4200ms infinite;
}
@media screen and (min-width: 760px) {
	.home_main h3 {
		font-size: 3.5em;
	}
}
.home_main ul {
	height: 100%;
	border-radius: 10%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 4em;
}
.home_main ul .projectLink {
	width: 100%;
	height: 200px;
	background: #920d8b;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 20px 0%;
	border-radius: 13%;
	color: #ffffff;
	text-decoration: none;
	grid-template-columns: repeat(5, 1fr);
}
.home_main ul .projectLink .projectImg {
	transform: translateX(0px) translateY(0px);
	transition: transform 210ms linear;
}
.home_main ul .projectLink #imgLeft {
	transform: scale(48%) translateY(-207px);
	grid-column: 2/5;
	display: flex;
